@import url('../../../global/texts.scss');
:host{
  font-size: 10px;
}
%btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4px;
  padding: 10px 16px;
  border-radius: 999px;
  width: 64vw;
  max-width: 255px;
  isolation: isolate;
  border: none;
  outline: none;
  transition: all 0.3s;
  cursor: pointer;
  box-shadow: inherit;
  margin: 0 auto;
}

.dav-buttonContainer {
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: flex-start;
  width: 100%;
  text-align: center;
  // padding-top: 16px;

  &--row {
    display: flex;
    gap: 8px;
    width: 100%;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  >p {
    @extend %defaultFont;
    font-size: var(--font-small);
  }
}

.dav-button {
  &__text {
    cursor: pointer;
    @extend %defaultFont;
    font-size: var(--font-small);
    text-align: center;

    &--small {
      @extend .dav-button__text;
      @extend %defaultFont;
      font-size: var(--font-xsmall);
    }
  }

  &--primary {
    @extend %btn;
    background-color: var(--red-primary);
    position: relative;
    overflow: hidden;
    @extend %defaultFont;
    font-weight: var(--fw-bold);
    color: #fff;
    text-align: center;

    &:hover {
      background: #B70412;
    }

    &:active {
      &:after {
        width: 500px;
        height: 500px;
      }
    }

    > :nth-child(n) {
      z-index: 1;
    }

    &:disabled {
      background-color: #d1d5e0;
      @extend %defaultFont;
      font-weight: var(--fw-bold);
      color: #838383;
      text-align: center;
      pointer-events: none;
      cursor: not-allowed;
    }
  }
  &--smallPrimary {
    @extend %btn;
    width: inherit;
    max-width: inherit;
    max-height: 32px;
    background-color: var(--red-primary);
    position: relative;
    overflow: hidden;
    @extend %defaultFont;
    font-weight: var(--fw-bold);
    color: #fff;
    text-align: center;

    &:hover {
      background: #B70412;
    }

    &:active {
      &:after {
        width: 500px;
        height: 500px;
      }
    }

    > :nth-child(n) {
      z-index: 1;
    }

    &:disabled {
      background-color: #d1d5e0;
      @extend %defaultFont;
      font-weight: var(--fw-bold);
      color: #838383;
      text-align: center;
      pointer-events: none;
      cursor: not-allowed;
    }
  }

  &--secondary {
    @extend %btn;
    @extend %defaultFont;
    font-weight: var(--fw-bold);
    color: #000000;
    text-align: center;
    background-color: initial;
    position: relative;
    overflow: hidden;

    &:hover {
      color: #000000;
      background: #F1F2F8;
    }

    &:active {
      &:after {
        width: 500px;
        height: 500px;
      }
    }

    > :nth-child(n) {
      z-index: 1;
    }

    &:disabled {
      background-color: transparent;
      @extend %defaultFont;
      font-weight: var(--fw-bold);
      color: #959393;
      text-align: center;
      cursor: not-allowed;
      cursor: not-allowed;
      pointer-events: none;
    }
  }

  &--outline {
    @extend %btn;
    background-color: transparent;
    border: 1px solid #404040;
    box-shadow: none;
    position: relative;
    overflow: hidden;
    @extend %defaultFont;
    font-weight: var(--fw-bold);
    color: #404040;

    &:hover {
      border: 1px solid #000000;
      color: #000000;
      background: #F1F2F8;
    }

    &:active {
      &:after {
        width: 500px;
        height: 500px;
      }
    }

    > :nth-child(n) {
      z-index: 1;
    }

    &:disabled {
      border: 1px solid #959393;
      @extend %defaultFont;
      font-weight: var(--fw-bold);
      color: #959393;
      cursor: not-allowed;
      pointer-events: none;
    }
  }
  &--smallOutline {
    @extend %btn;
    width: inherit;
    max-height: 32px;
    background-color: transparent;
    border: 1px solid #404040;
    box-shadow: none;
    position: relative;
    overflow: hidden;
    @extend %defaultFont;
    font-weight: var(--fw-bold);
    color: #404040;

    &:hover {
      border: 1px solid #000000;
      color: #000000;
      background: #F1F2F8;
    }

    &:active {
      &:after {
        width: 500px;
        height: 500px;
      }
    }

    > :nth-child(n) {
      z-index: 1;
    }

    &:disabled {
      border: 1px solid #959393;
      @extend %defaultFont;
      font-weight: var(--fw-bold);
      color: #959393;
      cursor: not-allowed;
      pointer-events: none;
    }
  }

  &--pill {
    position: relative;
    display: flex;
    align-items: center;
    isolation: isolate;
    border-radius: 16px;
    min-width: 136px;
    transition: all 0.3s;
    cursor: pointer;
    background-color: var(--white-ff);
    overflow: hidden;
    @include prefix((box-shadow: 2px 2px 12px 0 rgba(166, 171, 189, 0.34)), webkit moz ms o);
    @extend %defaultFont;
    font-size: var(--font-xsmall);
    font-weight: var(--fw-medium);
    color: var(--black-00);
    text-align: center;

    p {
      margin-left: 20px;
    }

    img {
      display: flex;
      left: 40px;
      top: 0;
      width: 30px;
      height: 30px;
      padding: 6px;
      background-color: var(--white-ff);
      border: 1px solid var(--gray-ef);
      border-radius: 50px;
    }

    &:hover {
      background-color: var(--gray-df);
      box-shadow: none;
    }

    &:disabled {
      opacity: 0.7;
      pointer-events: none;
      cursor: not-allowed;
    }
  }

  &--iconLarge {
    position: relative;
    isolation: isolate;
    transition: all 0.3s;
    cursor: pointer;
    background-color: var(--white-ff);
    width: 48px;
    height: 48px;
    border-radius: 50%;
    border: solid 1px var(--border-icon-button);
    display: flex;
    align-self: center;
    justify-content: center;
    align-items: center;
    overflow: hidden;

    img {
      display: inline-block;
      width: 24px;
      height: 24px;
    }

    &:hover {
      border: solid 1px var(--gray-ba);
    }

    &:active {
      background-color: var(--white-f0);
    }

    &:disabled {
      pointer-events: none;
      opacity: 58%;
      cursor: not-allowed;
      border: solid 1px var(--gray_f5);

      &~.dav-button__text {
        opacity: 58%;
        cursor: not-allowed;
      }
    }
  }
  &--iconLargeGrey {
    position: relative;
    isolation: isolate;
    transition: all 0.3s;
    cursor: pointer;
    background-color: #ebecf0;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    //border: solid 1px var(--border-icon-button);
    display: flex;
    align-self: center;
    justify-content: center;
    align-items: center;
    overflow: hidden;

    img {
      display: inline-block;
      width: 24px;
      height: 24px;
    }

    &:hover {
      border: solid 1px var(--gray-ba);
    }

    &:active {
      background-color: var(--white-f0);
    }

    &:disabled {
      pointer-events: none;
      opacity: 58%;
      cursor: not-allowed;
      border: solid 1px var(--gray_f5);

      &~.dav-button__text {
        opacity: 58%;
        cursor: not-allowed;
      }
    }
  }

  &--iconSmall {
    @extend .dav-button--iconLarge;
    width: 24px;
    height: 24px;
    border: solid 1px var(--border-icon-button-small);

    img {
      width: 16px;
      height: 16px;
    }
  }

  &--iconSimple {
    position: relative;
    cursor: pointer;
    background-color: transparent;
    width: 32px;
    height: 32px;
    border-radius: 50%;

    img {
      display: inline-block;
      width: 18px;
      height: 18px;
    }

    &:hover {
      background-color: var(--background-button-action-hover);
    }

    &:active {
      background-color: var(--gray-df);
    }

    &:disabled {
      pointer-events: none;
      opacity: 58%;
      cursor: not-allowed;
    }
  }

  &--back {
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding: 4px 8px 4px 8px;
    margin-right: 16px;
    border-radius: 4px;
    @extend %defaultFont;


    &:hover {
      background-color: var(--background-button-action-hover);
    }

    &:active {
      background-color: var(--gray-df);
    }

    &:disabled {
      pointer-events: none;
      opacity: 58%;
      cursor: not-allowed;
    }
  }
  &--backVivi {
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding: 4px 8px 4px 0px;
    border-radius: 4px;
    @extend %defaultFont;


    &:hover {
      background-color: var(--background-button-action-hover);
    }

    &:active {
      background-color: var(--gray-df);
    }

    &:disabled {
      pointer-events: none;
      opacity: 58%;
      cursor: not-allowed;
    }
  }

  &__ripple {
    position: absolute;
    top: var(--mouse-y);
    left: var(--mouse-x);
    display: block;
    margin: -100px;
    width: 200px;
    height: 200px;
    border-radius: 100px;
    background: rgba(40, 40, 40, 0.5);
    transform: scale(0);
    pointer-events: none;
    animation: clickeffect 1s ease;
    -webkit-animation: clickeffect 1s ease;
  }
}

@keyframes clickeffect {
  from {
    opacity: 0.7;
    transform: scale(0);
  }

  to {
    opacity: 0;
    transform: scale(2);
  }
}

@-webkit-keyframes clickeffect {
  from {
    opacity: 0.7;
    transform: scale(0);
  }

  to {
    opacity: 0;
    transform: scale(2);
  }
}
